import { IUser } from "../global/interfaces";
import BASE_URL from "../global/API";

export class UsersRepository {
  private API = `${BASE_URL}/users`

  public getAll (): Promise<IUser[]> {
    const response = fetch(this.API)
    const data = response.then(data => data.json())
    return data
  }

  public getById (id: string | null): Promise<IUser> {
    const response = fetch(`${this.API}/${id}`)
    const data = response.then(data => data.json())
    return data
  }

  public post (nome: string, email: string, cpf: string, senha: string): Promise<string> {
    const response = fetch(this.API, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ nome, email, cpf, senha })
    })
    const data = response.then(data => data.json())
    const userId = data.then(data => data.id)
    return userId
  }

  public put (payload: Object, id?: string | null) {
    const response = fetch(`${this.API}/${id}`, {
      method: 'put',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(payload)
    })
    const data = response.then(data => data.json())
    const userId = data.then(data => data.id)
    return userId
  }

  public async verifyLogin (email: string, senha: string): Promise<any> {
    const response = await fetch(this.API)
    const data  = await response.json()

    const isValid = data?.filter((dataUser: IUser) => {
      return dataUser.email?.toLowerCase() === email?.toLowerCase() && dataUser.senha === senha
    })

    return isValid
  }

  public verifyIfExist (userEmail: string): Promise<boolean> {
    const response = fetch(this.API)
    const data = response.then(data => data.json())
    const emailList = data.then(responseData => {
      return responseData.map((data: IUser) => data.email)
    })
    const isExist = emailList.then(email => email.indexOf(userEmail) !== -1)
    return isExist
  }

  public delete (id: string): Promise<void> {
    const response = fetch(`${this.API}/${id}`, {
      method: 'delete',
      headers: { 'Content-Type': 'application/json' }
    })
    const data = response.then(data => data.json())
    const userId = data.then(data => data.id)
    return userId
  }
}