import { IPurchase } from "../../../global/interfaces";

interface ReadOnlyRowProps {
  purchase: IPurchase
  handleEditClick(event: any, user: IPurchase): void
  handleDeleteClick(purchaseId: string): void
}

export default function ReadOnlyRow({ purchase, handleEditClick, handleDeleteClick }: ReadOnlyRowProps) {
  return (
    <tr key={purchase._id}>
      <td>{purchase.endereco}</td>
      <td>{purchase.telefone}</td>
      <td>{purchase.userId}</td>
      <td>{purchase.selectedProductIds}</td>
      <td>{purchase.selectedProductQtds}</td>
      <td>
        <button
          type='button'
          onClick={(event) => handleEditClick(event, purchase)}
        >
          Edit
        </button>
        <button
          type='button'
          onClick={() => handleDeleteClick(purchase._id)}
        >
          Delete
        </button>
      </td>
    </tr>
  )
}