import { IPurchase } from "../global/interfaces";
import BASE_URL from "../global/API";

export class PurchasesRepository {
  private API = `${BASE_URL}/purchases`

  public getAll (): Promise<IPurchase[]> {
    const response = fetch(this.API)
    const data = response.then(data => data.json())
    return data
  }

  public getById (id: number): Promise<IPurchase> {
    const response = fetch(`${this.API}/${id}`)
    const data = response.then(data => data.json())
    return data
  }

  public post (endereco: string, telefone: string, userId: string, selectedProductIds: string, selectedProductQtds: string): Promise<number> {
    const response = fetch(this.API, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ endereco, telefone, userId, selectedProductIds, selectedProductQtds })
    })
    const data = response.then(data => data.json())
    const purchaseId = data.then(data => data.id)
    return purchaseId
  }

  public put (payload: Object, id?: string) {
    const response = fetch(`${this.API}/${id}`, {
      method: 'put',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(payload)
    })
    const data = response.then(data => data.json())
    const purchaseId = data.then(data => data.id)
    return purchaseId
  }

  public delete (id: string): Promise<void> {
    const response = fetch(`${this.API}/${id}`, {
      method: 'delete',
      headers: { 'Content-Type': 'application/json' }
    })
    const data = response.then(data => data.json())
    const purchaseId = data.then(data => data.id)
    return purchaseId
  }
}