import { useEffect, useState, ChangeEvent, FormEvent } from 'react'
import EditableRow from './editableRow'
import ReadOnlyRow from './readOnlyRow'
import { IPurchase } from '../../../global/interfaces'
import { PurchasesRepository } from '../../../repository/Purchases'
import styles from './styles.module.scss'

export default function PurchaseTable() {
  const [purchaseRepository] = useState<PurchasesRepository>(new PurchasesRepository())

  const [purchases, setPurchases] = useState<IPurchase[]>()
  const [addFormData, setAddFormData] = useState({
    endereco: '',
    telefone: '',
    userId: '',
    selectedProductIds: '',
    selectedProductQtds: ''
  })
  const [editFormData, setEditFormData] = useState({
    endereco: '',
    telefone: '',
    userId: '',
    selectedProductIds: '',
    selectedProductQtds: ''
  })
  const [editPurchaseId, setPurchaseId] = useState<string>()

  function handleAddFormChange(event: ChangeEvent<HTMLInputElement>) {
    event.preventDefault()

    const fieldNome: 'endereco' | 'telefone' | 'userId' | 'selectedProductIds' | 'selectedProductQtds'  = (event as any).target.getAttribute('name')
    const fieldValue = event.target.value

    const newFormData = { ...addFormData }
    newFormData[fieldNome] = fieldValue

    setAddFormData(newFormData)
  }

  function handleEditFormChange(event: ChangeEvent<HTMLInputElement>) {
    event.preventDefault()

    const fieldNome: 'endereco' | 'telefone' | 'userId' | 'selectedProductIds' | 'selectedProductQtds'  = (event as any).target.getAttribute('name')
    const fieldValue = event.target.value

    const newFormData = { ...editFormData }
    newFormData[fieldNome] = fieldValue

    setEditFormData(newFormData)
  }

  function handleAddFormSubmit(event: FormEvent<HTMLFormElement>) {
    event.preventDefault()

    const newPurchase = {
      endereco: addFormData.endereco,
      telefone: addFormData.telefone,
      userId: addFormData.userId,
      selectedProductIds: addFormData.selectedProductIds,
      selectedProductQtds: addFormData.selectedProductQtds,
      _id: purchases ? String(purchases.length + 1) : String(0)
    }

    const newPurchases = purchases && [...purchases, newPurchase]
    setPurchases(newPurchases)

    purchaseRepository.post(newPurchase.endereco, newPurchase.telefone, newPurchase.userId, newPurchase.selectedProductIds, newPurchase.selectedProductQtds)
  }

  function handleEditFormSubmit(event: FormEvent<HTMLFormElement>) {
    event.preventDefault()

    const editedPurchase = {
      _id: editPurchaseId,
      endereco: editFormData.endereco,
      telefone: editFormData.telefone,
      userId: editFormData.userId,
      selectedProductIds: editFormData.selectedProductIds,
      selectedProductQtds: editFormData.selectedProductQtds
    }

    const newPurchases = purchases && [...purchases]
    const index = purchases?.findIndex((purchase: IPurchase) => purchase._id === editPurchaseId)

    // @ts-ignore
    newPurchases[index] = editedPurchase
    setPurchases(newPurchases)
    setPurchaseId(undefined)

    const { _id, ...payload } = editedPurchase

    purchaseRepository.put(payload, _id)
  }

  function handleEditClick(event: any, purchase: IPurchase) {
    event.preventDefault()
    setPurchaseId(purchase._id)

    const formValues = {
      endereco: purchase.endereco,
      telefone: purchase.telefone,
      userId: purchase.userId,
      selectedProductIds: purchase.selectedProductIds,
      selectedProductQtds: purchase.selectedProductQtds
    }
    setEditFormData(formValues)
  }

  function handleCancelClick() {
    setPurchaseId(undefined)
  }

  function handleDeleteClick(purchaseId: string) {
    const newPurchases = purchases && [...purchases]

    const index = purchases?.findIndex(purchase => purchase._id === purchaseId)

    index && newPurchases?.splice(index, 1)
    setPurchases(newPurchases)

    purchaseRepository.delete(purchaseId)
  }

  useEffect(() => {
    const getData = async () => {
      const data = await purchaseRepository.getAll()
      console.log(data)
      setPurchases(data)
    }

    getData()
  }, [purchaseRepository])

  return (
    <>
      <div className={styles.container}>
        <form onSubmit={handleEditFormSubmit}>
          <table>
            <thead>
              <tr>
                <th>Endereco</th>
                <th>Telefone</th>
                <th>userId</th>
                <th>selectedProductIds</th>
                <th>selectedProductQtds</th>
                <th>Actions</th>
              </tr>
            </thead>
            <tbody>
              {purchases?.map((purchase: IPurchase) => (
                <>
                  {editPurchaseId === purchase._id ? (
                    <EditableRow
                      editFormData={editFormData}
                      handleEditFormChange={handleEditFormChange}
                      handleCancelClick={handleCancelClick}
                    />
                  ) : (
                    <ReadOnlyRow
                      purchase={purchase}
                      handleEditClick={handleEditClick}
                      handleDeleteClick={handleDeleteClick}
                    />
                  )}
                </>
              ))}
            </tbody>
          </table>
        </form>

        <h2>Adicionar pedido</h2>
        <form onSubmit={handleAddFormSubmit}>
          <input
            type='text'
            name='endereco'
            required
            placeholder='Endereco'
            onChange={handleAddFormChange}
          /> 
          <input
            type='text'
            name='telefone'
            required
            placeholder='Telefone'
            onChange={handleAddFormChange}
          /> 
          <input
            type='text'
            name='userId'
            required
            placeholder='User ID'
            onChange={handleAddFormChange}
          /> 
          <input
            type='text'
            name='selectedProductIds'
            required
            placeholder='Selected product IDs'
            onChange={handleAddFormChange}
          />
          <input
            type='text'
            name='selectedProductQtds'
            required
            placeholder='Selected product qtds'
            onChange={handleAddFormChange}
          />
          <button type='submit'>Adicionar</button>
        </form>
      </div>
    </>
  )
}