import { IProduct } from "../global/interfaces";
import BASE_URL from "../global/API";

export class ProductsRepository {
  private API = `${BASE_URL}/products`

  public getAll (): Promise<IProduct[]> {
    const response = fetch(this.API)
    const data = response.then(data => data.json())
    return data
  }

  public getById (id: string): Promise<any> {
    const response = fetch(`${this.API}/${id}`)
    const data = response.then(data => data.json())
    return data
  }

  public post (image: string, title: string, preco: string, quantidade: number): Promise<number> {
    const response = fetch(this.API, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ title, image, preco, quantidade })
    })
    const data = response.then(data => data.json())
    const productId = data.then(data => data.id)
    return productId
  }

  public put (payload: Object, id?: string) {
    const response = fetch(`${this.API}/${id}`, {
      method: 'put',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(payload)
    })
    const data = response.then(data => data.json())
    const productId = data.then(data => data.id)
    return productId
  }

  public delete (id: string): Promise<void> {
    const response = fetch(`${this.API}/${id}`, {
      method: 'delete',
      headers: { 'Content-Type': 'application/json' }
    })
    const data = response.then(data => data.json())
    const productId = data.then(data => data.id)
    return productId
  }
}