import { useEffect, useState, ChangeEvent, FormEvent } from 'react'
import EditableRow from './editableRow'
import ReadOnlyRow from './readOnlyRow'
import { IUser } from '../../../global/interfaces'
import { UsersRepository } from '../../../repository/Users'
import styles from './styles.module.scss'

export default function UserTable() {
  const [usersRepository] = useState<UsersRepository>(new UsersRepository())

  const [users, setUsers] = useState<IUser[]>()
  const [addFormData, setAddFormData] = useState({
    nome: '',
    email: '',
    cpf: '',
    senha: ''
  })
  const [editFormData, setEditFormData] = useState({
    nome: '',
    email: '',
    cpf: '',
    senha: ''
  })
  const [editUserId, setUserId] = useState<string>()

  function handleAddFormChange(event: ChangeEvent<HTMLInputElement>) {
    event.preventDefault()

    const fieldNome: 'nome' | 'email' | 'cpf' | 'senha'  = (event as any).target.getAttribute('name')
    const fieldValue = event.target.value

    const newFormData = { ...addFormData }
    newFormData[fieldNome] = fieldValue

    setAddFormData(newFormData)
  }

  function handleEditFormChange(event: ChangeEvent<HTMLInputElement>) {
    event.preventDefault()

    const fieldNome: 'nome' | 'email' | 'cpf' | 'senha'  = (event as any).target.getAttribute('name')
    const fieldValue = event.target.value

    const newFormData = { ...editFormData }
    newFormData[fieldNome] = fieldValue

    setEditFormData(newFormData)
  }

  function handleAddFormSubmit(event: FormEvent<HTMLFormElement>) {
    event.preventDefault()

    const newUser = {
      nome: addFormData.nome,
      email: addFormData.email,
      cpf: addFormData.cpf,
      senha: addFormData.senha,
      _id: users ? String(users.length + 1) : String(0)
    }

    const newUsers = users && [...users, newUser]
    setUsers(newUsers)

    usersRepository.post(newUser.nome, newUser.email, newUser.cpf, newUser.senha)
  }

  function handleEditFormSubmit(event: FormEvent<HTMLFormElement>) {
    event.preventDefault()

    const editedUser = {
      _id: editUserId,
      nome: editFormData.nome,
      email: editFormData.email,
      cpf: editFormData.cpf,
      senha: editFormData.senha
    }

    const newUsers = users && [...users]
    const index = users?.findIndex((user: IUser) => user._id === editUserId)

    // @ts-ignore
    newUsers[index] = editedUser
    setUsers(newUsers)
    setUserId(undefined)

    const { _id, ...payload } = editedUser

    usersRepository.put(payload, _id)
  }

  function handleEditClick(event: any, user: IUser) {
    event.preventDefault()
    setUserId(user._id)

    const formValues = {
      nome: user.nome,
      email: user.email,
      cpf: user.cpf,
      senha: user.senha,
    }

    setEditFormData(formValues)
  }

  function handleCancelClick() {
    setUserId(undefined)
  }

  function handleDeleteClick(userId: string) {
    const newUsers = users && [...users]

    const index = users?.findIndex(user => user._id === userId)

    index && newUsers?.splice(index, 1)
    setUsers(newUsers)

    usersRepository.delete(userId)
  }

  useEffect(() => {
    const getData = async () => {
      const data = await usersRepository.getAll()
      setUsers(data)
    }

    getData()
  }, [usersRepository])

  return (
    <>
      <div className={styles.container}>
        <form onSubmit={handleEditFormSubmit}>
          <table>
            <thead>
              <tr>
                <th>Nome</th>
                <th>Email</th>
                <th>CPF</th>
                <th>Senha</th>
                <th>Actions</th>
              </tr>
            </thead>
            <tbody>
              {users?.map((user: IUser) => (
                <>
                  {editUserId === user._id ? (
                    <EditableRow
                      editFormData={editFormData}
                      handleEditFormChange={handleEditFormChange}
                      handleCancelClick={handleCancelClick}
                    />
                  ) : (
                    <ReadOnlyRow
                      user={user}
                      handleEditClick={handleEditClick}
                      handleDeleteClick={handleDeleteClick}
                    />
                  )}
                </>
              ))}
            </tbody>
          </table>
        </form>

        <h2>Adicionar usuÃ¡rio</h2>
        <form onSubmit={handleAddFormSubmit}>
          <input
            type='text'
            name='nome'
            required
            placeholder='Nome'
            onChange={handleAddFormChange}
          /> 
          <input
            type='text'
            name='email'
            required
            placeholder='Email'
            onChange={handleAddFormChange}
          /> 
          <input
            type='text'
            name='cpf'
            required
            placeholder='CPF'
            onChange={handleAddFormChange}
          /> 
          <input
            type='password'
            name='senha'
            required
            placeholder='Senha'
            onChange={handleAddFormChange}
          />
          <button type='submit'>Adicionar</button>
        </form>
      </div>
    </>
  )
}